import React from 'react'
import classes from './Tweet.module.css'
import TopPanel from './TopPanel/TopPanel'
import BottomPanel from './BottomPanel/BottomPanel'

const Tweet = (props) => {
    let description = props.data.full_text.trim();
    if (props.data.retweeted_status) {
        description = props.data.retweeted_status.full_text.trim();
    }
    return (
        <div className={classes.Tweet}>
            <TopPanel data={props.data.user}/>
            <BottomPanel description={description} />
        </div>

    )
}
export default Tweet;