import React from 'react'
import classes from './VaccModal.module.css'
import { Typeahead } from 'react-bootstrap-typeahead'; // ES2015
import 'react-bootstrap-typeahead/css/Typeahead.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import toast, { Toaster } from 'react-hot-toast';
const axios = require('axios');

const VaccModal = (props) => {

    const vaccinate = (type, data) => {
        if (type == null || data.ageGroup.length === 0 || data.email === "" || data.pinCode === '') {
            return
        }
        return new Promise((resolve, reject) => {
            axios.post('/vaccinated', { type: type, data: data })
                .then(function (response) {
                    if (response.data === 500) {
                        reject()
                        toast.error('No available vaccines near you')
                    } else if (response.data === 700) {
                        reject()
                        toast.error('Already Registered with this email')
                    }
                    else if (response.data === 'error') {
                        reject()
                        toast.error('Data send Failed')
                    } else {
                        resolve()
                    }
                })
                .catch(function (error) {
                    reject()
                })
        })
    }


    const vaccinated = (type, data) => toast.promise(vaccinate(type, data), {
        loading: 'Loading',
        success: type !== 'now' ? 'Registered Successfully' : "Mail sent Successfully",
        error: 'Error when fetching',
    });

    return (
        <form onSubmit={(e) => { e.preventDefault() }} className={classes.VaccModal}>
            <div className={classes.StateClass}>
                <p>Pin Code :</p>
                <input value={props.pinState} onChange={(e) => { props.pinChange(e.target.value) }}
                    autoComplete="off" autoCorrect="off" spellCheck="off"
                    className={classes.StateInput} type="text" name="pin"
                    placeholder="Pin" required />
            </div>
            <div className={classes.StateClass}>
                <p>Choose Age Group(yrs) :</p>
                <Typeahead
                    inputProps={{ autoComplete: "off", autoCorrect: "off", spellCheck: "off" }}
                    className={classes.TypeAhead}
                    id="basic-typeahead-single"
                    onChange={props.modalSelectAgeSelected}
                    options={["18-45", "45+"]}
                    placeholder="Choose your age-group"
                    selected={props.modalSelectAge}
                />
            </div>
            <div className={classes.StateClass}>
                <p>Email : </p>
                <input autoComplete="off" autoCorrect="off" spellCheck="off"
                    value={props.emailState} onChange={(e) => { props.emailStateChange(e.target.value) }}
                    className={classes.StateInput} type="email" name="email"
                    placeholder="Enter Email" required />
            </div>
            <div className={classes.ButtonGroup}>
                <button type="submit" onClick={(e) => {
                    if (props.modalSelectAge.length === 0 ||
                        props.pinState === "" ||
                        props.emailState === "") {
                        return toast.error('Error ! Fields Missing')
                    }
                    vaccinated("now", {
                        ageGroup: props.modalSelectAge
                        , pinCode: props.pinState
                        , email: props.emailState
                    })
                }} className={classes.Button}>Send current details</button>
                <button type="submit" onClick={(e) => {
                    if (props.modalSelectAge.length === 0 ||
                        props.pinState === "" ||
                        props.emailState === "") {
                        return toast.error('Error ! Fields Missing')
                    }
                    vaccinated("calender", {
                        ageGroup: props.modalSelectAge
                        , pinCode: props.pinState
                        , email: props.emailState
                    })
                }} className={classes.Button}>Notify Me When available</button>
            </div >
            <div style={{ height: "30px" }}></div>
            <Toaster position="bottom-center"
                reverseOrder={false} />
        </form>
    )
}

export default VaccModal
