import React from 'react'
import TweetsRow from '../TweetsRow/TweetsRow'
import Aux from '../../hoc/Auxilliary/Auxilliary'
import styled from 'styled-components'
import classes from './TweetPanel.module.css'
const StyledP = styled.p`
color:grey;
position:inherit;
`
const TweetPanel = (props) => {
    return (
        <div className={classes.TweetPanel}>
            <div><StyledP>All Data sourced from twitter.com</StyledP></div>

            {props.showLoader ? <Aux>
                <h3 className={classes.dataLoader}>Loading Leads</h3>
                <div className={classes.lds_ripple}><div></div><div></div></div>
            </Aux> :
                props.rows.length > 0 ? props.rows.map((row, id) =>
                    <TweetsRow data={row} key={id}></TweetsRow>) :
                    <Aux>
                        <h3 className={classes.dataLoader}>No leads Found</h3>
                        <p>(Kindly alter your search and retry)</p>
                    </Aux>
            }
            {props.rows.length > 0 ? <footer>**Created by <a href="https://www.linkedin.com/in/pulkit-pahuja-58b687148/">Pulkit Pahuja</a></footer> : null}
        </div>
    )
}
export default TweetPanel;