import React, { useEffect, useState } from 'react'
import Header from '../Header/Header'
import Filter from '../Filters/Filter'
import Layout from '../../hoc/Layout/Layout'
import TweetPanel from '../TweetPanel/TweetPanel'
import Modal from '../UI/Modal/Modal'
import styled from 'styled-components'
import VaccModal from '../VaccModal/VaccModal'
const axios = require('axios');

const StyledP = styled.p`
text-align:center;
font-size:18px;
`;

const MainPage = (props) => {
    let [rows, updateRows] = useState([]);
    let [showLoader, updateLoader] = useState(true)
    let [showModal, modalHandler] = useState(false)
    const [multiSelected, setMultiSelected] = useState(["Oxygen"]);
    const [SingleSelected, setSingleSelected] = useState(["Delhi"]);
    const [modalSelectAge, modalSelectAgeSelected] = useState([]);
    const [refresh, refreshPage] = useState(null)
    const [pinState, pinChange] = useState("");
    const [emailState, emailStateChange] = useState("");

    const closeModalHandler = () => {
        modalHandler(false);
    }

    const openModalHandler = () => {
        modalHandler(true);
    }

    useEffect(() => {
        updateLoader(true)
        axios.post('/tweets', { state: SingleSelected, requirements: multiSelected })
            .then(function (response) {
                // handle success
                updateLoader(false)
                if (response.data.length > 0) {
                    updateRows(response.data)
                } else {
                    updateRows([])
                }
            })
            .catch(function (error) {
                // handle error
                updateRows([])
            })
        return () => {
        }
    }, [SingleSelected, multiSelected, refresh])

    return (
        <Layout>
            <Modal modalClosed={closeModalHandler} show={showModal}>
                <StyledP>Notification for vaccination in your area</StyledP>
                <StyledP>Get an email whenever a slot for your Covid Vaccine is available </StyledP>
                <VaccModal modalSelectAge={modalSelectAge}
                    modalSelectAgeSelected={modalSelectAgeSelected}
                    pinState={pinState}
                    pinChange={pinChange}
                    emailState={emailState}
                    emailStateChange={emailStateChange}
                />
            </Modal>
            <Header refresh={refreshPage} openModal={openModalHandler} />
            <Filter SingleSelected={SingleSelected} setSingleSelected={setSingleSelected}
                multiSelected={multiSelected} setMultiSelected={setMultiSelected}
            />
            <TweetPanel rows={rows} showLoader={showLoader} />
        </Layout>
    );
};

export default MainPage;