import React from 'react'
import Tweet from './Tweet/Tweet'
import classes from './TweetRow.module.css'
const TweetPanel = (props) => {
    return (
        <div className={classes.TweetRow}>
            {props.data.map((tweet, id) => <Tweet data={tweet} key={id}></Tweet>)}
        </div>
    )
}
export default TweetPanel;