import React from 'react'
import classes from './Filter.module.css'
import CitySelector from './FilterComponents/CitySelector/CitySelector'
import TypeSelector from './FilterComponents/TypeSelector/TypeSelector'

const Filter = (props) => {

    return (
        <div className={classes.Filter}>
            <CitySelector selectedState={props.SingleSelected} selectItem={props.setSingleSelected} />
            <TypeSelector selectedState={props.multiSelected} selectItem={props.setMultiSelected} />
        </div>
    )
}
export default Filter;