import React from 'react'
import classes from './Navlink.module.css'

const Navlinks = (props) => {
    return (
        <a onClick={props.clicked ? props.clicked : null} className={[classes.Navlink, props.active ? classes.Active : null].join(" ")} href={props.link}>{props.text}</a>
    )
}
export default Navlinks;